<template>
  <div>
      <barraPrincipal/>
      <div class="containter p-4">
          <b-button pill variant="outline-secondary" @click="modal=true, nuevo = true"><b-icon-plus></b-icon-plus> Nuevo item</b-button>
      </div>
      <b-table
      id="table-transition-example" 
      name="table-transition-example"
      :tbody-transition-props="transProps"
      :fields="fields" 
      :items="productos"
      hover
      small
      class="text-center"
      >
      <template v-slot:cell(opciones)="codigo">
        <!-- `data.value` is the value after formatted by the Formatter -->
        <b-button variant="danger"  @click="borrar(codigo.item.codigo)">Borrar</b-button>&nbsp
        <b-button variant="success" @click="editar(codigo.item), nuevo = false">Editar</b-button>
      </template>
      </b-table>
      <b-modal v-model="modal"
        @show="resetModal"
        @hidden="resetModal"
        >
        <template v-slot:modal-footer>
        <div class="w-100">
          <b-button
            v-if="nuevo"
            variant="primary"
            size="sm"
            class="btn-block"
            @click="handleOk"
          >
            Guardar
          </b-button>
          <b-button
            v-if="!nuevo"
            variant="info"
            size="sm"
            class="btn-block"
            @click="reguardar"
          >
            Guardar edicion
          </b-button>
          <b-button
            variant="danger"
            size="sm"
            class="btn-block"
            @click="modal=false, resetModal"
          > 
            Cancelar
          </b-button>
        </div>
      </template>
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
            :state="codigoState"
            label="Codigo"
            label-for="codigo-input"
            invalid-feedback="Debes escribir un codigo"
            >
                <b-form-input
                    id="codigo-input"
                    v-model="codigo"
                    :state="codigoState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
            :state="enfoqueState"
            label="Enfoque"
            label-for="enfoque-input"
            invalid-feedback="Debes elegir alguno"
            >
                <b-form-input
                    id="enfoque-input"
                    v-model="enfoque"
                    :state="enfoqueState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
            :state="tallaState"
            label="Talla"
            label-for="talla-input"
            invalid-feedback="Debes Elegir alguna"
            >
                <b-form-input
                    id="talla-input"
                    v-model="talla"
                    :state="tallaState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
            :state="materialesState"
            label="Materiales"
            label-for="materiales-input"
            invalid-feedback="debes escribir los materiales"
            >
                <b-form-input
                    id="materiales-input"
                    v-model="materiales"
                    :state="materialesState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
            :state="descripcionState"
            label="Fabricado en"
            label-for="descripcion-input"
            invalid-feedback="debes escribir su lugar de fabrica"
            >
                <b-form-input
                    id="descripcion-input"
                    v-model="descripcion"
                    :state="descripcionState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
            :state="categoriaState"
            label="Categoria"
            label-for="categoria-input"
            invalid-feedback="Elige una categoria"
            >
                <b-form-input
                    id="categoria-input"
                    v-model="categoria"
                    :state="categoriaState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
            :state="marcaState"
            label="Marca"
            label-for="marca-input"
            invalid-feedback="Elige una marca"
            >
                <b-form-input
                    id="marca-input"
                    v-model="marca"
                    :state="marcaState"
                    required
                ></b-form-input>
            </b-form-group>
            <b-form-group
            :state="precioState"
            label="Precio"
            label-for="precio-input"
            invalid-feedback="Escribe el valor"
            >
                <b-form-input
                    id="precio-input"
                    v-model="precio"
                    :state="precioState"
                    required
                ></b-form-input>
            </b-form-group>
        </form>
      </b-modal>
  </div>
</template>

<script>
import barraPrincipal from '../components/barraPrincipal.vue'
import endPoint from '../../src/uri/uri.js'
export default {
    name:'PanelAdmin',
    components :{
        barraPrincipal
    },
    created(){
        this.uri = endPoint.uri
    },
    mounted(){
      this.mostrarTodos()
    },
    data(){
        return{
            nuevo: true,
            descripcion:'',
            descripcionState:null,
            precio:'',
            marca:'',
            categoria:'',
            materiales:'',
            enfoque:'',
            talla:'',
            codigo:'',
            name: '',
            codigoState: null,
            tallaState: null,
            enfoqueState: null,
            materialesState: null,
            categoriaState: null,
            marcaState: null,
            precioState: null,
            modal:false,
            sortBy: 'talla',
            sortDesc: false,
            transProps: {
                name: 'flip-list'
            },
            uri:'',
            productos:[],
            Producto:false,
            fields: [
                // A virtual column that doesn't exist in items
                //'index'
                // A virtual column made up from two fields
                { key: 'codigo', sortable: true, },
                { key: 'enfoque', sortable: true, },
                { key: 'descripcion', sortable: true, },
                { key: 'talla', sortable: true, variant:'info' },
                { key: 'materiales', sortable:false },
                { key: 'categoria', sortable: true, },
                { key: 'marca', sortable: true, },
                { key: 'precio', sortable: true, variant:'danger' },
                { key: 'opciones', variant:'info'}
            ],
        }
    },
    methods:{
        mostrarTodos(){
            this.axios.get(this.uri + '/api/productos/todos')
            .then(res=>{
                this.productos = res.data
                if(this.productos.length>0){
                    this.Producto = true
                }else{
                    this.Producto = false
                }
            })
            .catch(err=>{console.log(err)})
        },
        borrar(cod){
            const codigo = {
                cod: cod,
            }
            this.axios.post(this.uri + '/api/productos/borrarProducto', codigo)
            .then(res=>{
                console.log('producto eliminado')
                this.mostrarTodos()
            })
            .catch(err=>{console.log('error al borrar producto')})
        },
        editar(prod){
            console.log(prod)
            setTimeout(() => {
            this.codigo = prod.codigo
            this.enfoque = prod.enfoque
            this.descripcion = prod.descripcion
            this.talla = prod.talla
            this.materiales = prod.materiales
            this.categoria = prod.categoria
            this.marca = prod.marca
            this.precio = prod.precio  
            },300);
            this.modal  =true
        },
        resetModal() {
            this.codigo = "",
            this.enfoque = "",
            this.talla = "",
            this.materiales= "",
            this.categoria ="",
            this.marca= "",
            this.precio = ""
            this.descripcion = ""
            this.descripcionState = null
            this.codigoState = null,
            this.enfoqueState =null,
            this.tallaState =null,
            this.materialesState= null,
            this.categoriaState =null,
            this.marcaState= null,
            this.precioState =null
        },
        handleSubmit() {
            // Exit when the form isn't valid
            if(!this.checkFormValidity()){
                console.log(this.checkFormValidity)
                return
            }
            const product = {
                codigo : this.codigo,
                enfoque : this.enfoque,
                talla : this.talla,
                materiales: this.materiales,
                descripcion: this.descripcion,
                categoria : this.categoria,
                marca: this.marca,
                precio : this.precio,
            }
            if(this.nuevo){
               console.log(product) 
               console.log('es nuevo') 
                this.axios.post(this.uri + '/api/productos/guardarProducto', product)
                .then(res=>{
                    console.log('producto guardado con exito')
                    this.modal = false
                    this.mostrarTodos()
                    this.resetModal()
                })
                .catch(err=>{console.log(err)}) 
            }else{
               console.log('es editadoo') 
                console.log(product) 
                this.axios.post(this.uri + '/api/productos/reguardarProducto', product)
                .then(res=>{
                    console.log('producto editado con exito')
                    this.modal = false
                    this.mostrarTodos()
                    this.resetModal()
                })
                .catch(err=>{console.log(err)})
            } 
        },
        handleOk(bvModalEvt) {
            // Prevent modal from closing
            bvModalEvt.preventDefault()
            // Trigger submit handler
            this.handleSubmit()
        },
        checkFormValidity() {
            if( this.codigo == ''){
                this.codigoState = false
            }else{
                this.codigoState = true
            }
            if( this.enfoque == ''){
                this.enfoqueState = false
            }else{
                this.enfoqueState = true
            }
            if( this.talla == ''){
                this.tallaState = false
            }else{
                this.tallaState = true
            }
            if( this.materiales == ''){
                this.materialesState = false
            }else{
                this.materialesState = true
            }
            if( this.categoria == ''){
                this.categoriaState = false
            }else{
                this.categoriaState = true
            }
            if( this.marca == ''){
                this.marcaState = false
            }else{
                this.marcaState = true
            }
            if( this.precio == ''){
                this.precioState = false
            }else{
                this.precioState = true
            }
            if( this.descripcion == ''){
                this.descripcionState = false
            }else{
                this.descripcionState = true
            }
            if(this.codigo ==''|| this.enfoque ==''|| this.talla ==''|| this.materiales ==''|| this.categoria ==''|| this.marca ==''|| this.precio ==''|| this.descripcion ==''){
                return false
            }else{
                return true
            }
        },
        reguardar(bvModalEvt){
            // Prevent modal from closing
            bvModalEvt.preventDefault()
            // Trigger submit handler
            this.handleSubmit()
        },
    }
}
</script>
