<template>
  <div class="Masculino"> 
    <barra-principal/>
    <h2 class="text-center bg-info text-white"><strong>Calzado infantil</strong></h2>
    <div v-if="Producto" class="p-4">
        <div class="row p-4">
            <mostrar-producto class="col-md-2" :producto="producto" v-for="producto in productos" :key="producto._id" />
        </div>
    </div>
    <div v-if="! Producto">
        <h2><strong>Sin existencia de Calzado infantil</strong></h2>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import barraPrincipal from '../components/barraPrincipal.vue'
import mostrarProducto from '../components/mostrarProducto.vue'
import endPoint from '../../src/uri/uri.js'
export default {
  name: 'Infantil',
  components: {
    barraPrincipal,
    mostrarProducto,
  },
  created(){
      this.uri = endPoint.uri
  },
  mounted(){
      this.mostrarInfantil()
  },
  data(){
      return{
          uri:'',
          productos:[],
          Producto:false,
      }
  },
  methods:{
      mostrarInfantil(){
          this.axios.get(this.uri + '/api/productos/Infantil')
          .then(res=>{
              this.productos = res.data
              if(this.productos.length>0){
                  this.Producto = true
              }else{
                  this.Producto = false
              }
            })
          .catch(err=>{console.log(err)})
      },
  }
}
</script>